.custom-input {
  width: 25rem;
  padding: 12px 16px;
  margin: 15px;
  border: 2px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
  outline: none;
  transition: border-color 0.3s ease;
  resize: none;
}

.custom-input:focus {
  border-color: #4f46e5; /* nice purple-blue */
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.2);
}

.custom-btn {
  background-color: #4f46e5;
  color: white;
  padding: 12px 24px;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.custom-btn:hover {
  background-color: #4338ca;
  transform: scale(1.03);
}

.custom-btn:active {
  transform: scale(0.98);
}

.card {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.08);
  padding: 24px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  height: 40%;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.error {
  color: red;
}
